import { Fragment, useState } from "react";
import classes from "./NewQuote.module.css";
import Modal from "../UI/Modal";
import { useDispatch, useSelector } from "react-redux";
import { addItem } from "../../store/items-slice";
import Table from "../UI/Table";
import { quotationHeaders } from "../../store/quote-slice";

const NewQuote = (props) => {
  const [vehicleNoIsValid, setVehicleNoIsValid] = useState();
  const [customerNameIsValid, setCustomerNameIsValid] = useState();
  const [customerMobileIsValid, setCustomerMobileIsValid] = useState();
  const [customerAddressIsValid, setCustomerAddressIsValid] = useState();
  const [validating, setValidating] = useState(false);
  const [formIsValid, setFormIsValid] = useState(false);
  const [enteredVehicleNo, setEnteredVehicleNo] = useState("");
  const [enteredCustomerName, setEnteredCustomerName] = useState("");
  const [enteredCustomerMobile, setEnteredCustomerMobile] = useState("");
  const [enteredCustomerAddress, setEnteredCustomerAddress] = useState("");
  const [enteredQuoteDetails, setEnteredQuoteDetails] = useState([]);
  const [quotationAmount, setQuotationAmount] = useState(0);
  const [enteredPartsName, setEnteredPartsName] = useState("");
  const [enteredPartNo, setEnteredPartNo] = useState("");
  const [enteredUnit, setEnteredUnit] = useState("");
  const [enteredQty, setEnteredQty] = useState("");
  const [enteredPrice, setEnteredPrice] = useState("");
  const [selectedCustomer, setSelectedCustomer] = useState({});
  const [selectedCustomerId, setSelectedCustomerId] = useState();
  const [validPartAdd, setValidPartAdd] = useState(false);

  const dispatch = useDispatch();

  const columns = useSelector(quotationHeaders);

  const vehicleNoChangeHandler = (event) => {
    setEnteredVehicleNo(event.target.value.toUpperCase());
    const details = enteredQuoteDetails;

    setFormIsValid(
      event.target.value.length >= 8 &&
        enteredCustomerName.length >= 3 &&
        enteredCustomerMobile.length === 10 &&
        enteredCustomerAddress.length > 3 &&
        details.length >= 1
    );
  };

  const customerNameChangeHandler = (event) => {
    setEnteredCustomerName(event.target.value.toUpperCase());
    const details = enteredQuoteDetails;

    setFormIsValid(
      event.target.value.length >= 3 &&
        enteredVehicleNo.length >= 8 &&
        enteredCustomerMobile.length === 10 &&
        enteredCustomerAddress.length > 3 &&
        details.length >= 1
    );
  };
  const customerMobileChangeHandler = (event) => {
    setEnteredCustomerMobile(event.target.value);
    const details = enteredQuoteDetails;

    setFormIsValid(
      enteredVehicleNo.length >= 8 &&
        enteredCustomerName.length >= 3 &&
        event.target.value.length === 10 &&
        enteredCustomerAddress.length > 3 &&
        details.length >= 1
    );
  };
  const customerAddressChangeHandler = (event) => {
    setEnteredCustomerAddress(event.target.value);
    const details = enteredQuoteDetails;

    setFormIsValid(
      enteredVehicleNo.length >= 8 &&
        enteredCustomerName.length >= 3 &&
        enteredCustomerMobile.length === 10 &&
        event.target.value.length > 3 &&
        details.length >= 1
    );
  };
  const deleteItem = (item, index) => {
    const details = JSON.parse(JSON.stringify(enteredQuoteDetails));
    details.splice(index, 1);
    const totalAmount = details.reduce((total, item) => {
      return total + item.qty * item.price;
    }, 0);
    setEnteredQuoteDetails(details);
    setQuotationAmount(totalAmount);
    // console.log("item", item, index, details);
  };
  const partsAddClickHandler = async (event) => {
    const details = enteredQuoteDetails;
    details.push({
      name: enteredPartsName,
      partNo: enteredPartNo,
      unit: enteredUnit,
      qty: enteredQty,
      price: enteredPrice,
    });

    const totalAmount = details.reduce((total, item) => {
      return total + item.qty * item.price;
    }, 0);
    setEnteredQuoteDetails(details);
    setQuotationAmount(totalAmount);

    setFormIsValid(
      enteredVehicleNo.length >= 8 &&
        enteredCustomerName.length >= 3 &&
        enteredCustomerMobile.length === 10 &&
        enteredCustomerAddress.length > 3 &&
        details.length >= 1
    );
    clearPartForm();
  };

  const clearPartForm = () => {
    setEnteredPartsName("");
    setEnteredPartNo("");
    setEnteredUnit("");
    setEnteredQty("");
    setEnteredPrice("");
    setValidPartAdd(false);
  };

  const partsNameChangeHandler = (event) => {
    setEnteredPartsName(event.target.value.toUpperCase());
    setValidPartAdd(
      event.target.value.length > 2 &&
        enteredUnit.length > 0 &&
        enteredQty.length > 0 &&
        enteredPrice.length > 0
    );
  };

  const partNoChangeHandler = (event) => {
    setEnteredPartNo(event.target.value.toUpperCase());
  };

  const unitChangeHandler = (event) => {
    setEnteredUnit(event.target.value.toUpperCase());
    setValidPartAdd(
      enteredPartsName.length > 2 &&
        event.target.value.length > 0 &&
        enteredQty.length > 0 &&
        enteredPrice.length > 0
    );
  };

  const qtyChangeHandler = (event) => {
    setEnteredQty(event.target.value);
    setValidPartAdd(
      enteredPartsName.length > 2 &&
        event.target.value.length > 0 &&
        enteredUnit.length > 0 &&
        enteredPrice.length > 0
    );
  };

  const priceChangeHandler = (event) => {
    setEnteredPrice(event.target.value);
    setValidPartAdd(
      enteredPartsName.length > 2 &&
        event.target.value.length > 0 &&
        enteredUnit.length > 0 &&
        enteredQty.length > 0
    );
  };

  const validateVehicleNoHandler = () => {
    setVehicleNoIsValid(enteredVehicleNo.length >= 8);
  };
  const validateCustomerNameHandler = () => {
    setCustomerNameIsValid(enteredCustomerName.length >= 3);
  };
  const validateCustomerMobileHandler = () => {
    setCustomerMobileIsValid(enteredCustomerMobile.length === 10);
  };
  const validateCustomerAddressHandler = () => {
    setCustomerAddressIsValid(enteredCustomerAddress.length > 3);
  };

  const selectCustomer = (obj) => {
    if (obj.length > 0) {
      setSelectedCustomer(obj);
      setEnteredCustomerName(obj.name);
      setEnteredCustomerMobile(obj.mobile);
      setEnteredCustomerAddress(obj.address);
      setEnteredVehicleNo(obj.vehicleNo);
      setSelectedCustomerId(obj._id);
    }
  };
  // const item = useSelector((state) => findItem(state, {}));

  const submitHandler = async (event) => {
    event.preventDefault();
    setValidating(true);
    if (formIsValid) {
      const payload = {
        customer: selectedCustomerId,
        vehicleNo: enteredVehicleNo,
        customerName: enteredCustomerName,
        customerMobile: enteredCustomerMobile,
        customerAddress: enteredCustomerAddress,
        details: JSON.stringify(enteredQuoteDetails),
        quotationAmount: quotationAmount,
      };

      await dispatch(addItem(payload));
      props.onClose();
    }
  };

  const modalAction = (
    <div className={`${classes.actions} ${classes["m-t-1"]}`}>
      {!validating && (
        <button
          className={classes.button}
          type="submit"
          disabled={!formIsValid}
        >
          Save
        </button>
      )}
      {validating && (
        <button className={classes.button} type="button" disabled={validating}>
          Saving...
        </button>
      )}
    </div>
  );

  const newQuoteModalContent = (
    <Fragment>
      <h3 className={classes.heading}>Add New Quotation</h3>
      <form onSubmit={submitHandler}>
        <div className={`card ${classes["m-t-1"]}`}>
          <div className="card-header">Customer Details</div>
          <div className={` ${classes["p-1"]} card-body`}>
            <div className="row">
              <div className="col-6">
                <div
                  className={`${classes.control} ${
                    vehicleNoIsValid === false
                      ? classes.invalid
                      : vehicleNoIsValid === true
                      ? classes.valid
                      : ""
                  } ${vehicleNoIsValid === false ? classes.bump : ""}`}
                >
                  <label htmlFor="vehicleNo">Vehicle No</label>
                  <input
                    type="text"
                    id="vehicleNo"
                    value={enteredVehicleNo}
                    autoComplete="off"
                    onChange={vehicleNoChangeHandler}
                    onBlur={validateVehicleNoHandler}
                  />
                </div>
                <div
                  className={`${classes.control} ${
                    customerMobileIsValid === false
                      ? classes.invalid
                      : customerMobileIsValid === true
                      ? classes.valid
                      : ""
                  } ${customerMobileIsValid === false ? classes.bump : ""}`}
                >
                  <label htmlFor="customerMobile">Customer Mobile</label>
                  <input
                    type="text"
                    id="customerMobile"
                    value={enteredCustomerMobile}
                    autoComplete="off"
                    onChange={customerMobileChangeHandler}
                    onBlur={validateCustomerMobileHandler}
                  />
                </div>
              </div>
              <div className="col-6">
                <div
                  className={`${classes.control} ${
                    customerNameIsValid === false
                      ? classes.invalid
                      : customerNameIsValid === true
                      ? classes.valid
                      : ""
                  } ${customerNameIsValid === false ? classes.bump : ""}`}
                >
                  <label htmlFor="customerName">Customer Name</label>
                  <input
                    type="text"
                    id="customerName"
                    value={enteredCustomerName}
                    autoComplete="off"
                    onChange={customerNameChangeHandler}
                    onBlur={validateCustomerNameHandler}
                  />
                </div>
                <div
                  className={`${classes.control} ${
                    customerAddressIsValid === false
                      ? classes.invalid
                      : customerAddressIsValid === true
                      ? classes.valid
                      : ""
                  } ${customerAddressIsValid === false ? classes.bump : ""}`}
                >
                  <label htmlFor="customerAddress">Customer Address</label>
                  <input
                    type="text"
                    id="customerAddress"
                    value={enteredCustomerAddress}
                    autoComplete="off"
                    onChange={customerAddressChangeHandler}
                    onBlur={validateCustomerAddressHandler}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className={`card ${classes["m-t-1"]}`}>
          <div className="card-header">Parts Details</div>
          <div className={`card-body ${classes["p-1"]}`}>
            <div className="row">
              <div className="col-6">
                <div className={`${classes.control}`}>
                  <label htmlFor="partsName">Parts Name</label>
                  <input
                    type="text"
                    id="partsName"
                    value={enteredPartsName}
                    autoComplete="off"
                    onChange={partsNameChangeHandler}
                  />
                </div>
                <div className={`${classes.control}`}>
                  <label htmlFor="partNo">Part No</label>
                  <input
                    type="text"
                    id="partNo"
                    value={enteredPartNo}
                    autoComplete="off"
                    onChange={partNoChangeHandler}
                  />
                </div>
                <div className={`${classes.control}`}>
                  <label htmlFor="unit">Unit</label>
                  <input
                    type="text"
                    id="unit"
                    value={enteredUnit}
                    autoComplete="off"
                    onChange={unitChangeHandler}
                  />
                </div>
              </div>
              <div className="col-6">
                <div className={`${classes.control}`}>
                  <label htmlFor="qty">Quantity</label>
                  <input
                    type="number"
                    id="qty"
                    value={enteredQty}
                    autoComplete="off"
                    onChange={qtyChangeHandler}
                    min="1"
                  />
                </div>
                <div className={`${classes.control}`}>
                  <label htmlFor="price">Price</label>
                  <input
                    type="text"
                    id="price"
                    value={enteredPrice}
                    autoComplete="off"
                    onChange={priceChangeHandler}
                  />
                </div>
              </div>
            </div>
            <div className={`${classes.actions} ${classes["actions-right"]}`}>
              <button
                className={classes.button}
                type="button"
                onClick={partsAddClickHandler}
                disabled={!validPartAdd}
              >
                + Add
              </button>
            </div>
          </div>
          <Table
            data={enteredQuoteDetails}
            columns={columns}
            paginator={false}
            deleteItem={deleteItem}
          />
          <div className={`card-footer ${classes["actions-right"]}`}>
            Total Amount: {`${quotationAmount.toFixed()}`}
          </div>
        </div>
        {modalAction}
      </form>
    </Fragment>
  );

  return (
    <Modal onClose={props.onClose} big={true}>
      {newQuoteModalContent}
    </Modal>
  );
};

export default NewQuote;
